import React, { Component } from "react";
import { Field, formValueSelector, reduxForm } from "redux-form";
import { connect } from "react-redux";
import { renderField } from "../../renderField";
import { getrekeningList } from "../../actions/rekeningAction";
import { createNumberMask } from "redux-form-input-masks";

const required = (value) =>
  value === 0 || value === "0" ? undefined : "Tidak Boleh Lebih Dari Total";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  locale: "id-ID",
});
class Validasi extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidMount() {
    this.props.dispatch(getrekeningList());
  }
  updateUI() {
    this.props.change("sisaBon", this.props.totalBayar);
    this.props.change("totalBon", this.props.totalPembayaran);
  }
  render() {
    return (
      <div className="col-lg-12 col-md-12">
        <form onSubmit={this.props.handleSubmit}>
          <div className="row">
            <div className="col-lg-4 col-md-4">
              <Field
                name="cash"
                label="Cash"
                component={renderField}
                type="phone"
                onChange={this.updateUI()}
                {...currencyMask}
              />
            </div>
            {this.props.from === "transfer" ? (
              <>
                <div className="col-lg-4 col-md-4">
                  <Field
                    name="transfer"
                    label="Transfer"
                    component={renderField}
                    type="phone"
                    onChange={this.updateUI()}
                    {...currencyMask}
                  />
                </div>
                <div className="col-lg-4 col-md-4">
                  <Field
                    name="tanggal"
                    label="Tanggal"
                    component={renderField}
                    type="date"
                  ></Field>
                </div>
                <div className="col-lg-4 col-md-4">
                  <Field
                    name="rekening_asal"
                    label="A/C Asal"
                    component={renderField}
                    type="text"
                    readOnly
                  />
                </div>
                <div className="col-lg-3">
                  <Field
                    name="rekening_tujuan"
                    component={renderField}
                    type="text"
                    label="A/C Tujuan"
                    placeholder="Masukan Rekening Tujuan"
                    readOnly
                  />
                </div>
                <div className="col-lg-4 col-md-4"></div>
              </>
            ) : (
              <>
                <div className="col-lg-4 col-md-4">
                  <Field
                    name="giro"
                    label="Giro"
                    component={renderField}
                    type="number"
                  />
                </div>
                <div className="col-lg-4 col-md-4">
                  <Field
                    name="noGiro"
                    label="No Giro"
                    component={renderField}
                    type="number"
                  />
                </div>
                <div className="col-lg-4 col-md-4">
                  <Field
                    name="bank"
                    label="Bank"
                    component={renderField}
                    type="number"
                  />
                </div>
                <div className="col-lg-4 col-md-4">
                  <Field
                    name="tanggalJ/T"
                    label="Tanggal Jatuh Tempo"
                    component={renderField}
                    type="number"
                  />
                </div>
                <div className="col-lg-4 col-md-4">
                  <Field
                    name="tanggal"
                    label="Tanggal"
                    component={renderField}
                    type="date"
                  />
                </div>
                <div className="col-lg-4 col-md-4">
                  <div className="form-group">
                    <label htmlFor="">A/C Tujuan</label>
                    <Field
                      name="a/cTujuan"
                      component="select"
                      className="form-control"
                    >
                      <option value="DEFAULT" key="DEFAULT">
                        Silahkan Pilih
                      </option>
                    </Field>
                  </div>
                </div>
                <div className="col-lg-8 col-md-8">
                  <div></div>
                </div>
              </>
            )}
            <div className="col-lg-4 col-md-4">
              <Field
                name={this.props.from === "transfer" ? "totalBon" : "awal"}
                label={this.props.from === "transfer" ? "Total Bayar" : "Awal"}
                component={renderField}
                type="telp"
                readOnly={true}
                {...currencyMask}
              />
            </div>
            <div className="col-lg-4 col-md-4">
              <Field
                name="totalBayar"
                label="Total Bon"
                component={renderField}
                type="telp"
                readOnly={true}
                {...currencyMask}
              />
            </div>
            <div className="col-lg-4 col-md-4">
              <Field
                name={this.props.from === "transfer" ? "sisaBon" : "akhir"}
                label={this.props.from === "transfer" ? "Sisa Bon" : "Akhir"}
                component={renderField}
                type="telp"
                readOnly={true}
                validate={required}
                {...currencyMask}
              />
            </div>
            <div className="col-lg-3 d-none">
              <Field
                name="no_trx"
                component={renderField}
                type="text"
                label="Nomor Trx"
                placeholder="Masukan Nomor Trx"
              />
            </div>
            <div className="col-lg-12 col-md-12">
              <div className="text-right">
                <button className="btn btn-primary" type="submit">
                  Bayar
                </button>
              </div>
            </div>
          </div>
        </form>
      </div>
    );
  }
}

Validasi = reduxForm({
  form: "Validasi",
  enableReinitialize: true,
})(Validasi);
const selector = formValueSelector("Validasi");
export default connect((state) => {
  const { cash, transfer, totalBayar } = selector(
    state,
    "cash",
    "transfer",
    "totalBayar"
  );
  return {
    initialValues: {
      totalBon: state.provinsi.listValidasiTransferSelected.saldo_rp,
      tanggal: state.provinsi.listValidasiTransferSelected.tanggal,
      transfer: state.provinsi.listValidasiTransferSelected.saldo_rp,
      rekening_asal: state.provinsi.listValidasiTransferSelected.rek_asal,
      rekening_tujuan: state.provinsi.listValidasiTransferSelected.rek_tujuan,
      totalBayar: state.provinsi.listValidasiTransferSelected.saldo_rp,
      no_trx: state.provinsi.listValidasiTransferSelected.no_trx,
      cash: 0,
      sisaBon: 0,
    },
    rekening: state.provinsi.rekening,
    totalBayar:
      parseFloat(totalBayar || 0) -
      (parseFloat(cash || 0) + parseFloat(transfer || 0)),
    totalPembayaran: parseFloat(cash || 0) + parseFloat(transfer || 0),
  };
})(Validasi);
