import React, { Component } from "react";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import { getValidasiTransferCustomer } from "../actions/validasiTransferCustomer";
import { gagal } from "../Alert";
import { AxiosMasterPost } from "../Axios";
import HeaderCard from "../HeaderCard";
import FormValidasiTransferCustomer from "./Component/FormValidasiTransferCustomer";
import Validasi from "./Element/Validasi";

class ValidasiTransferCustomer extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  handleSubmit(data) {
    const hasil = {
      cash_rp: data.cash,
      transfer_rp: data.transfer,
      tanggal_transfer: data.tanggal,
      rek_asal: data.rekening_asal,
      rek_tujuan: data.rekening_tujuan,
      total_transfer: data.totalBon,
      total_bayar: parseFloat(data.cash || 0) + parseFloat(data.transfer || 0),
      sisa_transfer: data.sisaBon,
    };
    AxiosMasterPost(
      `validate-bank-outstand-transfer/validate/${String(
        data.no_trx
      ).toLowerCase()}`,
      hasil
    )
      .then(() =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Berhasil Validasi Data!",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() => {
            document.getElementById("dismiss_modal").click();
          })
          .then(() => {
            this.props.dispatch(getValidasiTransferCustomer());
          })
      )
      .catch((err) => gagal(`Gagal Validasi Transfer , Detial : ${err}`));
  }

  render() {
    return (
      <div className="container-fluid">
        <div className="col-lg-12 col-d-12">
          <div className="card">
            <HeaderCard title="Validasi Transfer" />
            <div className="card-body">
              <FormValidasiTransferCustomer
                onSubmit={(data) => this.handleSubmit(data)}
              />
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Validasi Transfer
                </h4>
                <button
                  type="button"
                  id="dismiss_modal"
                  name="dismiss_modal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <Validasi
                  onSubmit={(data) => this.handleSubmit(data)}
                  from="transfer"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect()(ValidasiTransferCustomer);
